<template>
    <div class="container">
        
        <div id="heroText"> I </div>
        <div id="name">Najeebullah Khan</div>
        <img src="../../public/img/icons/nk2.png" alt="">
    </div>
</template>

<script>

    export default {
    
        methods: {
            typeItIn: function() {
                let moreInfo = ["am a Software Engineer","am Passionate About Technology","Love AI and Robotics"]
                let counter = 0;
                typInf();
                function typInf() {
                    let element = document.getElementById("heroText");
                    let cuurentArray = "";
                    let index = 0;
                    let task;
                    cuurentArray = Array.from(moreInfo[counter]);
                    task = setInterval(typeTwo,150);

                    function typeTwo(){
                            if(index == cuurentArray.length){
                                clearInterval(task);      
                                setTimeout(() => {
                                    deleteText(2);
                                }, 1000);          
                            }else{
                                element.innerHTML += cuurentArray[index];
                                index++;
                            }
                        }
                }

                function deleteText(limit){
                    let element = document.getElementById("heroText");
                    let cuurent = element.innerHTML;
                    console.log(cuurent);
                    let taskTwo = setInterval(delText,80);

                    function delText(){
                        if(cuurent.length <= limit){
                            console.log("stoping");
                            clearInterval(taskTwo);
                            counter++;
                            if(counter === 3){
                                counter = 0;
                            }
                        typInf();
                        }else{
                            element.innerHTML = cuurent;
                            cuurent = cuurent.slice(0,cuurent.length-1);
                        }
                    }
                    
                }  
            }
        },
        mounted (){
        this.typeItIn();    
        }
    }
</script>


<style scoped>
.container{
    
    display: flex;
    width:65%;
    height:50vh;
    margin:10% auto;
    flex-direction: column;
    flex: space-between;
}

#heroText {
    display: flex;
    color:#a06faf;
    height: 80%;
    text-align: left;
    font-size: 50px;
    font-weight: 700;
    
}
#name{
    display: flex;
    font-size: 30px;
    font-style: italic;
    margin: 2%;
    color:#a06faf;
    
}

img{
    width: 250px;
    height: 300px;
    border-radius: 140px;
    margin-left: auto;
    }

@media only screen and (max-width:600px){
    .container{
    
    display: flex;
    width:100%;
    height:50vh;
    margin:10% auto;
    flex-direction: column;
    align-items: center;
    
    flex: space-between;
    }
    #heroText {
    display: flex;
    color:#a06faf;
    width:80%;
    height: 30%;
    text-align: left;
    
    font-size: 30px;
    font-weight: 700;
    
    }
    #name{
        display: flex;
        font-size: 30px;
        font-style: italic;
        margin: 2%;
        color:#a06faf;
        
    }
    img{
        width:200px;
        height:200px;
        margin-right:auto;
        margin-left: auto;
        }
}
</style>