<template>
        <div id="main">
            <div id="menu"  v-on:click = "showMenu()">

            </div>
            <div class="navContainer">
                <div id = "cont" class="container" v-on:click = "showMenu()">
                    <div class="NavItem"  @click="printIt('home')">Home</div>
                    <div class="NavItem" @click="printIt('education')">Education</div>
                    <div class="NavItem" @click="printIt('skills')">Skills</div>
                    <div class="NavItem" @click="printIt('projects')">Work</div>
                    <div class="NavItem" v-on:click="printIt('contact')">Contact</div>
                </div>
            </div>
        </div>
        
    
</template>

<script>
export default {
    isVisible : false,
    contact : "contact",
    methods: {
        
        showMenu: function () {
            let element = document.getElementById("cont");
            let element2 = document.getElementById("menu");
            let task;
            let index = 0;
            if(!this.isVisible){
                
                element.style.opacity = 0;
                element.style.visibility = "visible";
                element2.style.visibility = "hidden";
                element2.style.height = "0px";
                task = setInterval(moveRight,10);
                this.isVisible = true;
                
            }else{
                element.style.visibility = "hidden";
                element2.style.visibility = "visible";
                element2.style.height = "30px";
                this.isVisible = false;

            }
            function moveRight(){
                    if(index === 1){
                        clearInterval(task);
                    }else {
                        element.style.opacity = index ;
                        index += 0.1;
                    }
                }
            
        },
        printIt : function(contacts){
                console.log("clicked clicked");
                this.$emit('clicked',contacts);
                console.log("emitted emitted");
            }
        
    }
}
</script>
<style scoped>
.main{
    display: flex;
}
.navContainer {
    display: flex;
    width:100%;
    height:60px;
    background-color:#002147;
    
    
}
.container{
    
    display: flex;
    background-color:#002147;
    color: #a06faf;
    width: 100%;
    height: 60px;
    margin-left: auto;
    margin-right:auto;
    border-radius: 25px;
    justify-content: center;
    align-items: center;
    flex:space-around;
    
}
.NavItem {
    align-self: left;
    display: flex;
    font-size: 20px;
    padding: .9%;
    font-weight: 700;
    transition: padding 0.2s;
    transition-timing-function: ease-in;
    
}
.NavItem:hover {
     color: #002147;
     background-color:#a06faf;
     padding: 1.3%;
     cursor: pointer; 
    }



@media only screen and (max-width: 600px) {
  .navContainer {
    background-color: #002147;
    visibility: hidden;
  }
  .container {
      visibility: hidden;
      position: fixed;
  }

 #menu {
    background-image: url("../../public/img/icons/menue2.png");
    background-size:contain;
    background-repeat: no-repeat;
    position: fixed;
    width:10%;
    height:30px;
 }

 .container {
     visibility:hidden;
     width:100%;
    }

 .NavItem {
     font-size: 10px;
     padding: 2%;
 }

 
}
</style>