<template>
    <div class = "container">
        <div class="title">Contact Form</div>
        <form action="" @submit = "submitForm">
            <input type="text" name="pName" v-model="pName" id="PName" placeholder = "Your name ..." onfocus="this.placeholder = ''"
            onblur="this.placeholder = 'Your name...'">
            <input type="email" v-model="email" id="email" placeholder="Email Address..."
            onfocus="this.placeholder = ''"
            onblur="this.placeholder = 'Email Address...'"
            >
            <input type="text" id="subject" v-model="subject" placeholder="Subject..." onfocus="this.placeholder = ''"
            onblur="this.placeholder = 'Subject...'">
            <textarea id="message" v-model="message" cols="30" rows="10" placeholder="Type your message here..." onfocus="this.placeholder = ''"
            onblur="this.placeholder = 'Type your message here...'"></textarea>
            
            <button type="submit" class="submit" >Submit</button>
        </form>
    </div>
</template>
<script>
    import swal from 'sweetalert';
    export default {
        pName:  null,
        message: null,
        email: null,
        subject: null,
        methods: {
            submitForm: function(e){
                e.preventDefault();
                console.log( this.pName + "\n" + this.message);
                let connection = new WebSocket("wss://ow59r47268.execute-api.us-east-1.amazonaws.com/dev");
                
                
                let response = {
                    
                    name: this.pName,
                    mail: this.email,
                    subject: this.subject,
                    message: this.message
                
                }
                
                let mess = {
                    action : "sendMail",
                    message : response
                }
                connection.onopen = function(event){
                    console.log("connection open");
                    connection.send(JSON.stringify(mess));
                    swal(
                        {
                            title: 'Thank you!',
                            text: 'Message sent. Thanks for reaching out.',
                            icon: 'success'  
                        }
                    );
                }
                
                console.log("message sent "+ JSON.stringify(mess));
                e.target.reset();    
            }
        }
        
    }
</script>
<style scoped>

.container{
    display: flex;
    width: 100%;
    align-items: center;
    flex-direction: column;
    margin-top: 2%;
    background-color: #fff;
    height: 70vh;
    
}
.title{
    font-size:40px;
    color:#002147;
    font-weight: bold;
    padding: 20px;
 

    }
form {
    display: flex;
    flex-direction: column;
    width: 50%;
    
    background-color:#002147;
    align-items: center;
    
}
form input {
    width: 80%;
    height: 40px;
    margin: 10px;
    padding: 2%;
    background-color: #002147;
    color: #a06faf;
    border: none;
    border-bottom: 2px solid #a06faf;
    font-size: 25px;
    flex: space-around;
    

}
::placeholder{
    color: #a06faf;
    opacity: 0.5;
}
input:focus, textarea:focus, select:focus{
        outline: none;
    }
#message {
    width: 80%;
    margin: 20px;
    padding: 2%;
    background-color: #002147;
    color: #a06faf;
    font-size: 25px;
    flex: space-around;
    border: none;
    border-bottom: 2px solid #a06faf;
}

button {

    width: 85%;
    height: 40px;
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 15px;
    margin-top:10px;
    flex:space-around;
    
    border-radius: 20px;
    background-color: #fff;
    color: #a06faf;
}

.submit:hover {
    color:white;
    background-color: #a06faf;
    height: 50px;
    font-size: 30px;
    cursor:pointer;
}

@media only screen and (max-width:600px){
    form {
        display: flex;
        flex-direction: column;
        width: 90%;
        background-color:#002147;
        align-items: center;
        font-size: 10px;
    }
    form input, #message {
        font-size: 15px;
        padding:0;
        margin: 2%;
    }

}
</style>