<script>
export default {
    methods : {
        colorFlow: function (){
            let col1Ar = document.getElementsByClassName("col1");
            let col2Ar = document.getElementsByClassName("col2");
            let col3Ar = document.getElementsByClassName("col3");
            let colorList = ["yellow","purple","#fff","#008018","#0cf5e9","#a19a5e"];
            //let colorList = ["yellow","purple","#fff","#000","green","#0000F9"];
            let index = 0;
            let colorCount = 0;

            let task = setInterval(changeColor,500);

            function changeColor(){
                if(index > 8){
                    clearInterval(task);
                }else{
                    if(index >= 7){
                        
                        index = 0;
                        
                    }
                    if(colorCount >= 6){
                        colorCount = 0;
                    }
                    
                    //col1Ar[index].style.border =  "2px solid "+colorList[colorCount];
                    col1Ar[index].style.borderTop =  "5px solid "+colorList[colorCount];
                    col1Ar[index].style.color = colorList[colorCount];
                    //col2Ar[index].style.border =  "2px solid "+colorList[colorCount];
                    col2Ar[index].style.borderTop =  "5px solid "+colorList[colorCount];
                    col2Ar[index].style.color = colorList[colorCount];
                    //col3Ar[index].style.borderTop =  "2px solid "+colorList[colorCount];
                    col3Ar[index].style.borderTop =  "5px solid "+colorList[colorCount];
                    col3Ar[index].style.color = colorList[colorCount];

                    index++;
                    colorCount++;
                }
                
            }
        }
    },
    mounted(){
        this.colorFlow();
    }
}
</script>



